  import {
    location
  } from '../util/LocationUtil.js'
  Vue.filter('isZero', (data) => {
    if (data == 0) {
      return null
    } else {
      return data
    }
  })
  export default {
    name: 'Home',
    data() {
      return {

        shopname: null,
