    methods: {
      handleClick(tab, event) {
        console.log(tab, event)
      },
      ShowchangeLang(){
        this.changeLangShow=true
      },
      goHome() {
        this.$router.push('/orderlist')
      },
      download(){
        this.tipsShow = true
      },
      shopcart() {
        // console.log(this.$refs.cart.$el)
        this.$store.state.cartShow = !this.$store.state.cartShow
      },
      accounts() {
        if (this.$store.state.totalPrice != 0 ) {
       //   let login =  //

          if (this.$route.name == 'About') {
            this.$store.commit('updataDiningStyle', '')
          }
          if (this.$route.name == 'Wm') {
            this.$store.commit('updataDiningStyle', '')
          }

          if (!localStorage.getItem('userInfo')) {
            console.log('')
             this.$router.push('/login')
          } else {
            console.log('')
            this.$router.push('/orderconfirm')
          }

        }

      },
      gots() {
        if (this.$route.name == 'Wm') {
          if (confirm(this.$t('shop.switch_dinning_way_warnning'))) {
            this.$store.commit('clearListNum')
            this.$store.commit('clearCart')
            this.$router.push('/about')
          }
        } else {
          console.log('')
        }
      },
      gowm() {
        if (this.$route.name == 'About') {
          if (confirm(this.$t('shop.switch_dinning_way_warnning'))) {
            this.$store.commit('clearListNum')
            this.$store.commit('clearCart')
            this.$router.push('/Wm')
          }
        } else {
          console.log('')
        }

      },
      /***/
      // getLocation() {

      //   let geolocation = location.initMap("map-container"); //
      //   AMap.event.addListener(geolocation, "complete", result => {
      //       console.log(result)
      //   });

      // },
      getLocation() {
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(this.showPosition, this.showError, {
            enableHighAccuracy: true,
            maximumAge: 0,
          });
        } else {
          alert("");
        }
      },
      showPosition(position) {
        alert(`${position.coords.latitude}${position.coords.longitude}`)

      },

      showError(error)

      {

        switch (error.code)

        {

          case error.PERMISSION_DENIED:

            alert("User denied the request for Geolocation.")

            break;

          case error.POSITION_UNAVAILABLE:

            alert("Location information is unavailable.")

            break;

          case error.TIMEOUT:

            alert("The request to get user location timed out.")

            break;

          case error.UNKNOWN_ERROR:

            alert("An unknown error occurred.")

            break;

        }

      }
    },

    beforeCreate() {
      if(!this.$Cookies.get('language')){
        this.$Cookies.set('language','ja')
      }
    },
    mounted() {
       // this.getLocation()
      // this.getLocation()
